body {
    font-family: 'Open Sans', sans-serif;
    background-color: #f3f7fa;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
}

.item {
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-size: 13px;
    color: #4A4A4A;
    line-height: 36px;
    position: relative;
    padding: 0 15px;
}

.item:hover {
    background-color: #F1FCFF;
}

.input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    display: none;
}

.checkmark {
    position: relative;
    top: 4px;
    display: inline-block;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 19px;
    width: 19px;
    background: #FFFFFF;
    border: 1px solid #D2D5D6;
    border-radius: 3px;
    margin-right: 10px;
}

.input:checked + .checkmark {
    background: #F2FCFF;
    border: 1px solid #2196F3;
}

.checkmark:after {
    content: '';
    position: absolute;
    opacity: 0;
    top: 5px;
    left: 4px;
    width: 9px;
    height: 7px;
    background: url('../../../images/checkmark.svg') no-repeat;
    -webkit-transition: opacity .2s;
    -o-transition: opacity .2s;
    transition: opacity .2s;
}

.input:checked + .checkmark:after {
    opacity: 1;
}

.label {
    display: block;
    cursor: pointer;
}

.only {
    cursor: pointer;
    position: absolute;
    right: 0;
    top: 0;
    opacity: 0;
    padding: 0;
    border: none;
    background: none;
    text-transform: uppercase;
    font-size: 11px;
    color: #2196F3;
    margin-right: 15px;
    letter-spacing: 0.5px;
    line-height: 36px;
    -webkit-transform: translateY(25px);
                -ms-transform: translateY(25px);
            transform: translateY(25px);
    -webkit-transition: opacity .15s ease, -webkit-transform .15s ease-out;
    transition: opacity .15s ease, -webkit-transform .15s ease-out;
    -o-transition: opacity .15s ease, transform .15s ease-out;
    transition: opacity .15s ease, transform .15s ease-out;
    transition: opacity .15s ease, transform .15s ease-out, -webkit-transform .15s ease-out;
}

.only:focus {
    outline: none;
}

.item:hover .only {
    opacity: 1;
    -webkit-transform: translateY(0);
                -ms-transform: translateY(0);
            transform: translateY(0);
}


