body {
    font-family: 'Open Sans', sans-serif;
    background-color: #f3f7fa;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
}

.header {
    font-weight: 600;
    font-size: 12px;
    color: #4A4A4A;
    letter-spacing: 0.5px;
    line-height: 19px;
    text-transform: uppercase;
    margin: 15px 0 11px 15px;
    padding: 0;
}

.tabs {
    list-style: none;
    padding: 0;
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
    -ms-flex-direction: row;
    flex-direction: row;
    margin: 0 15px 15px 15px;
}

.tab {
    padding: 8px 20px;
    font-weight: 600;
    font-size: 12px;
    color: #2196F3;
    letter-spacing: 0.5px;
    line-height: 22px;
    text-transform: uppercase;
    background: #FFFFFF;
    -webkit-box-shadow: 0 0 0 1px #D2D5D6;
            box-shadow: 0 0 0 1px #D2D5D6;
    cursor: pointer;
    width: 66px;
}

.tab:first-child {
    border-radius: 5px 0 0 5px;
}

.tab:last-child {
    border-radius: 0 5px 5px 0;
}

.tab:hover:not(.tabActive) {
    background: #F2FCFF;
    -webkit-box-shadow: 0 0 0 1px #64B5F5;
            box-shadow: 0 0 0 1px #64B5F5;
    z-index: 2;
}

.tabActive {
    background: #2196F3;
    color: #FFFFFF;
    -webkit-box-shadow: 0 0 0 1px #2196F3;
            box-shadow: 0 0 0 1px #2196F3;
    z-index: 3;
}


